// import Bmob from '@/utils/bmob.js'
import postCard from '@/components/postCard';
export default {
	components: {
		postCard
	},
	data() {
		return {
			info: {
				objectId: '',	// 用户Id
			},
			commentValue: '',	// 评论内容
			postId: '',	// 帖子id
			postInfo: {},	// 帖子信息
			postAuthor: '',	// 发帖用户id
			commentsList: [],	// 评论列表
			isFavorite: false,	// 是否收藏
			userState: 0,	// 用户状态 0-正常 1-禁言
		}
	},
	computed: {
	},
	async onLoad(option) {
		let that = this;
		if(option.postId === "undefined"){
			that.noFound()
		}else{
			this.postId = option.postId ? option.postId : '8604d6bb67';
			uni.getStorage({
				key: 'userInfo',
				success: function (res) {
					that.info = res.data;
					that.getPostFavoriteStatus();
					that.checkStatus();
				}
			});
			this.getPost();
			this.getComments();
			this.updatePost('view')
		}
	},
	onReady(){
	},
	onShow(){
		this.checkStatus();
	},
	// 分享
	onShareAppMessage() {
		return {
			title: '邀请您使用小镇事事通',
			path: '/pages/post/postDetail?postId=' + this.postId
		}
	},
	// 到底
	onReachBottom(){
		// if (this.pageSetting.pageIndex <= this.pageSetting.totalPage) {
		// 	//设置列表数据
		// 	this.getPost()
		// }
	},
	methods: {
		toZone(item){
			if(this.info.objectId !== item.own.objectId){
				this.navTo('/pages/user/otherzone?userId=' + item.own.objectId)
			}else{
				uni.switchTab({ url: '/pages/user/myzone' })
			}
		},
		// 获取用户发言状态
		checkStatus(){
			let that = this;
			// 查询大于某个日期的数据，示例代码如下
			const query = that.Bmob.Query("_User");
			query.get(that.info.objectId).then(res => {
			  that.userState = res.state
			}).catch(err => {
			  console.log(err)
			})
		},
		// 获取帖子状态
		getPostFavoriteStatus(){
			let that = this;
			const query = that.Bmob.Query('favorite');
			query.equalTo("userId","==", that.info.objectId);
			query.equalTo("postIdStr","==", that.postId);
			query.find().then(res => {
				if(res.length === 0){
					that.isFavorite = false;
				}else{
					that.isFavorite = true;
				}
				uni.hideLoading();
			}).catch(err => {
			  
			})
		},
		// 获取帖子
		getPost() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			const query = that.Bmob.Query('postList');
			query.include("author", "_user");
			query.get(that.postId).then(res => {
				uni.hideLoading();
				that.postInfo = res;
				that.postAuthor = res.author.objectId;
			}).catch(err => {
				if(err.code === 101){
					that.noFound()
				}
				console.log(err)
			})
		},
		// 获取评论列表
		getComments () {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			const query = that.Bmob.Query("comment");
			query.equalTo("attrPost","==", that.postId);
			query.include("own", "_user");
			query.find().then(res => {
				that.commentsList = []
			    that.commentsList = res;
				uni.hideLoading();
			});
		},
		// 回复某人
		reply(item){
			this.commentValue = '@' +  item.own.nickName + ' '
		},
		// 发表评论
		sendComments () {
			let that = this;
			if(that.userState === 1){
				uni.showToast({
				    title: '由于违反相关法律法规，您已被禁言，如有疑问，可找客服申诉',
				    icon: 'none',
					duration: 3000
				});
				return 
			}
			uni.showLoading({
				title: '加载中',
				mask: true,
			});
			that.Bmob.checkMsg(that.commentValue).then(res => {
				// Pointer 类型在数据库是一个json数据类型，只需调用Pointer方法创建一个Pointer对象存入到字段中，如下：
				const pointerUser = that.Bmob.Pointer('_User')
				const pUserID = pointerUser.set(that.info.objectId)
				
				const pointerPost = that.Bmob.Pointer('postList')
				const pPostID = pointerPost.set(that.postId)
				
				var query = that.Bmob.Query('comment');
				
				query.set('own',pUserID)	// 绑定的用户id
				query.set('attrPost',pPostID)	// 绑定的帖子id
				query.set('attrPostStr',that.postId)	// 绑定的帖子id 字符串 用来删除
				query.set("content", that.commentValue)
				query.set("publishUser", that.info.nickName)	// 发表用户
				query.save().then(res => {
					// uni.hideLoading();
					uni.showToast({
						title: '评论成功',
						icon: 'success',
						duration: 1500
					})
					that.commentValue = '';
					setTimeout( () => {
						that.updatePost('comments');
						that.getComments();
					}, 1000);
				}).catch(err => {
				  console.log(err)
				})
			}).catch(err => {
				let tips = '错误类型：' + err.code + '，' + err.error;
				if(err.code === 10007){
					tips = '输入内容含有敏感词，请文明用语'
				}
				uni.showToast({
					title: tips,
					icon: 'none'
				})
			})
		},
		// 更新帖子表的评论数
		updatePost(param) {
			let that = this;
			var query = that.Bmob.Query('postList');		
			query.get(that.postId).then(res => {
			  res.increment(param)	// 原子计算 自加1 传入第二个参数,支持正负数，到increment方法来指定增加或减少多少，1是默认值。
			  res.save()
			}).catch(err => {
			  console.log(err)
			})
		},
		// 帖子添加收藏与取消收藏
		addFavorite(){
			uni.showLoading({
				title: '加载中'
			});
			let that = this;
			
			// 先查询是否收藏
			const query = that.Bmob.Query('favorite');
			query.equalTo("userId","==", that.info.objectId);
			query.equalTo("postIdStr","==", that.postId);
			
			const favorite = that.Bmob.Pointer('favorite')
			const fid = favorite.set(that.postId)
			// query.equalTo("postId","==", fid);
			
			query.find().then(res => {
				if(res.length === 0){
					// 添加收藏  实际插入记录 
					query.set('userId',that.info.objectId)	// 绑定的用户id -- 谁喜欢
					query.set('postId',fid)	// 绑定的用户id poster类型
					query.set('postIdStr',that.postId)	// 绑定的用户id string类型
					// query.set('whoLikesUserId', that.postInfo.author.objectId)	// 帖子作者
					
					const authorInfo = that.Bmob.Pointer('_User')
					const postAuthor = authorInfo.set(that.postAuthor)
					query.set('author',postAuthor)	// 绑定的发帖用户id poster类型
					
					query.save().then(res1 => {
						that.isFavorite = true;
						uni.hideLoading();
					})
				}else{
					// 取消收藏  实际删除记录
					query.destroy(res[0].objectId).then(res2 => {
					  that.isFavorite = false;
					  uni.hideLoading();
					})
				}
			}).catch(err => {
			  
			})
		},
		noFound(){
			uni.showModal({
			    title: '糟糕！',
			    content: '帖子找不到了',
				showCancel: false,
			    success: function (res) {
			        if (res.confirm) {
			            uni.navigateBack()
			        } 
			    }
			});
		}
	}
}
